import React, {useState} from 'react';
import s from './Users.module.css';
import {connect} from "react-redux";

const Users = (props) => {

    const [displayUsers, setDisplayUsers] = useState(false);

    const onClick = () => {
        console.log('1')
        setDisplayUsers(true);
    }

    return (
        <div>
            {displayUsers ? <div className={s.users}>{props.users.map((user) => <User user={user} key={user.id}/>)}</div>
                : <div><button onClick={onClick} className={s.button}>Загрузить пользователей</button></div>
            }
        </div>
    );
};

const User = (props) => {
    return (
        <div className={s.user}>
            <div className={s.header}>
                <div>{props.user.nickname}</div>
                <hr className={s.hr}/>
            </div>
            <div>id: {props.user.id},</div>
            <div>email: {props.user.email},</div>
            <div>pass: {props.user.pass}</div>
        </div>
    );
};

const mapStateToProps = (state) => {
    return {
        users: state.form.users
    }
}

export default connect(mapStateToProps, {})(Users);